<template>
  <div class="container">
    <div class="zero-outer" v-if="commodityList.length == 0">
      <p class="zero-p"></p>
    </div>
    <div class="list" v-for="(commodity) in commodityList" :key="commodity.shopCarId">
      <div class="check">
        <input ref="cb" :scId="commodity.shopCarId" :checked="isSeleceted" @click="selected(commodity,$event)"
          type="checkbox" />
      </div>
      <div class="time">
      </div>
      <div class="text">
        <div class="img">
          <!--  -->
          <img :src="commodity.scCommodityImg" alt="" />
        </div>
        <ul class="sp">
          <li>
            <!--  -->
            <div>{{commodity.commodity.commodityName}}</div>
            <span></span>
            <div class="fh"></div>
          </li>
          <li v-if="commodity.commodity.nowPrice != null && commodity.commodity.nowPrice != 0">
            <span>
              &nbsp;{{commodity.commodity.nowPrice}}
            </span>
            <br />
            <s>&nbsp;{{commodity.commodity.price}}</s>
          </li>
          <li v-else>
            <span>
              &nbsp;{{commodity.scCommodityPrice}}
            </span>
          </li>
          <li class="count">
            <div>
              <button class="sub" @click="scCommodityNumber('-',commodity,commodity.commodity,$event)">-</button>
              <span class="shu"> {{commodity.scCommodityNumber}} </span>
              <button class="add" @click="scCommodityNumber('+',commodity,commodity.commodity,$event)">+</button>
            </div>
          </li>
          <li class="lprice">
             {{commodity.scCommodityPrice * commodity.scCommodityNumber}}
          </li>
          <li><a href="#" class="del" @click="deleteComodity(commodity.shopCarId)"></a></li>
        </ul>
      </div>

      <!-- <div class="youhui">
          <label><input type="checkbox" /></label>
          <div class="bao">
            <i></i>
            <span></span>
          </div>
          <div class="baoPrice">
            <span></span><s>138.00</s>
          </div>
        </div> -->
    </div>

    <div class="foot">
      <div class="left">
        <input :checked="ischecked" @click="allSelect($event)" type="checkbox" />
        &nbsp;
        <span></span>
        &nbsp;
        <a href="#" @click="deleteAllSelect" style="color:black"></a>
      </div>
      <div class="right">
        <div class="right-left">
          <span class="zj">
            <label></label>
            <span>&nbsp;{{sum}}</span>
            <p>
              <strong>{{selectedList.length}}</strong>
            </p>
          </span>
        </div>
        <router-link class="ljjsjj" to="/order">
          <a @click="pay" class="right-right"></a>
        </router-link>

      </div>
    </div>
  </div>
</template>

<script>
  import axios from 'axios'
  import qs from 'qs'
  export default {
    props: [
    ],
    data() {
      return {
        commodityList: [],
        thisCommodityNumber: 0,
        isSeleceted: false,
        selectedList: [],
        sum: 0,
        ischecked:false
      };
    },
    mounted() {
      this.getAllCommodity()
      this.$bus.$on('allSelect', (e) => {
        //inputchecked
        //
        //
        let cbs = this.$refs.cb
        if (cbs != undefined && cbs.length >= 0) {
          for (let i = 0; i < cbs.length; i++) {
            //checkboxchecked
            const element = cbs[i];
            element.checked = e
            let has = this.selectedList.indexOf(parseInt(element.getAttribute("scId")))
            //
            if (e == false) {
              this.selectedList = []
              //
            } else if (e == true && has == -1) {
              let scId = parseInt(element.getAttribute("scId"))
              this.selectedList.push(scId)
            }
          }
        }
        this.allSum()
      })
    },

    methods: {
      //
      allSelect(event){
        let e = event.target.checked
        let cbs = this.$refs.cb
        if (cbs != undefined && cbs.length >= 0) {
          for (let i = 0; i < cbs.length; i++) {
            //checkboxchecked
            const element = cbs[i];
            element.checked = e
            let has = this.selectedList.indexOf(parseInt(element.getAttribute("scId")))
            //
            if (e == false) {
              this.selectedList = []
              //
            } else if (e == true && has == -1) {
              let scId = parseInt(element.getAttribute("scId"))
              this.selectedList.push(scId)
            }
          }
        }
        this.allSum()
      },
      //id
      getAllCommodity() {
        const user = JSON.parse(localStorage.getItem('huawei_login'))
        axios.get("/getAllShopCarCommodity/" + user.id).then((response) => {
          this.commodityList = response.data.result
        }).then((response)=>{
          console.log("#####################",response);
        })
      },
      //
      scCommodityNumber(operate, shopCommodity, commodity, event) {
        var commodityNumber = shopCommodity.scCommodityNumber
        const shopCarId = shopCommodity.shopCarId
        const limitNumber = commodity.limitNumber
        if (operate == '-') {
          if (commodityNumber <= 1) {
            return;
          } else {
            commodityNumber--
            this.sendCommodityNumber(commodityNumber, shopCarId)
          }
        } else if (operate == '+') {
          console.log();
          if (commodityNumber < limitNumber) {
            commodityNumber++
            this.sendCommodityNumber(commodityNumber, shopCarId)
          } else {
            alert('' + limitNumber + '')
          }
        }
      },
      //
      sendCommodityNumber(commodityNumber, shopCarId) {
        const user = JSON.parse(localStorage.getItem("huawei_login"))
        axios.get(`/changeCommodityNumber/${commodityNumber}/${shopCarId}/${user.id}`).then((response) => {
          this.commodityList = response.data.result
          this.allSum()
        })
      },
      //
      deleteComodity(shopCarId) {
        const user = JSON.parse(localStorage.getItem("huawei_login"))
        if (confirm('')) {
          axios.get(`/deleteShopCar/${shopCarId}/${user.id}`).then((response) => {
            console.log(response.data.result);
            this.commodityList = response.data.result
          })
        }
      },
      //
      selected(commodity, event) {
        if (event.target.checked) {
          this.selectedList.push(commodity.shopCarId)
          this.sum += commodity.scCommodityPrice * commodity.scCommodityNumber
        } else {
          let index = this.selectedList.indexOf(commodity.shopCarId)
          this.selectedList.splice(index, 1)
          this.sum -= commodity.scCommodityPrice * commodity.scCommodityNumber
        }
        console.log("this.selectedList", this.selectedList);
      },
      //
      pay() {
        let user = JSON.parse(localStorage.getItem("huawei_login"))
        //idjson
        var SCCommodityListStr = JSON.stringify(this.selectedList)
        //
        let isPay = true
        if (!confirm('')) {
          isPay = false
        }
        axios.post(`/addOrderOfShopCar?orderPrice=${this.sum}&dfk=${!isPay}&userId=${user.id}`,
          //qs 
          qs.stringify({ SCCommodityList: SCCommodityListStr }, { indices: false }))
      },
      //
      allSum() {
        this.sum = 0
        console.log(this.selectedList);
        for (let index = 0; index < this.commodityList.length; index++) {
          const element = this.commodityList[index]
          let has = this.selectedList.indexOf(element.shopCarId)
          if (has != -1) {
            this.sum += element.scCommodityPrice * element.scCommodityNumber
          }
        }
      },
      //
      deleteAllSelect(){
        console.log("");
        const user = JSON.parse(localStorage.getItem("huawei_login"))
        axios.post(`/deleteShopCars?userId=${user.id}`,
          //qs 
          qs.stringify({ SCCommodityList: this.selectedList }, { indices: false })).then((response)=>{
            console.log(response.data);
            this.commodityList = response.data.result
          })
      }

    },
    watch: {
      deep: true,
      //
      selectedList() {
         if(this.selectedList.length == this.commodityList.length){
          this.$bus.$emit("changeAllChecked", true)
          this.ischecked = true
         }else{
          this.$bus.$emit("changeAllChecked", false)
          this.ischecked = false
         }
      },
    }
  };
</script>

