<template>
  <div class="big">
    <!-- <h2>{{commofityOptions}}</h2>   -->
    <!--  -->
    <dl>
      <!--dl-->
      <label></label>
      <div class="op">
        <select v-model="city">
          <option value=""></option>
          <option value=""></option>
          <option value=""></option>
          <option value=""></option>
        </select>
      </div>
    </dl>
    <div class="baoyou">
      <span>48</span>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span></span>
      &nbsp;<a href="#"></a>
    </div>
    <div class="bigbianma">
      <span></span><span class="bianma">{{commodity.id}}</span>
    </div>
    <hr />
    <dl v-for="(option,index) in commodityOptions" :key="index">
      <label>{{option.optionName}}</label>
      <div class="in">
        <span>
          <a :class="index==0?'one_active':'one'"
            @click="selcetOptin(option.optionName,item),changeSty($event,index),sendPrice()" href="#" class="one"
            v-for="(item,index) in option.options" :key="index">
            {{item.option}}
          </a>
        </span>
      </div>
    </dl>
    <dl>
      <label for=""></label>
      <div class="select">
        <span v-for="(value,key) in selectedOptions" :key="key">
          {{value[1]}}
        </span>
      </div>
    </dl>

    <div class="footer d-flex">
      <!--  -->
      <div class="d-flex number position-relative">
        <span class="position-absolute showLimit" v-show="showLimit">
          {{showMessage}}
        </span>
        <div class="shu">
          <!--   -->
          {{commodityNumber}}
        </div>
        <div>
          <button @click="commodityNumberChange('+')" class="jiahao font-weight-bold">+</button>
          <button @click="commodityNumberChange('-')" class="jianhao font-weight-bold">-</button>
        </div>
      </div>
      <!--  -->
      <div class="container">
        <a @click="toShopCar">
          <input type="button" class="btn  jiaru" value="" />
        </a>
        <a>
          <input @click="toPay" type="submit" ref="placeOrder" class="btn xiadan" value="" />
        </a>
      </div>
    </div>
  </div>
</template>

<script>
  import axios from 'axios';
  export default {
    data() {
      return {
        city: '',
        selectedOptions: new Map(),
        optionActive: [],
        commodityNumber: 1,
        showLimit: false,
        showMessage: '',
        commodity: '',
        commodityPrice: 0,
        optionPrice: 0
      };
    },
    // props:["commodity"],
    computed: {
      commodityOptions() {
        return this.commodity.jsonOptions
      },
      sendPriceValue() {
        return this.commodityPrice + this.optionPrice
      }
    },
    methods: {
      //option 
      selcetOptin(optionKey, item) {
        //
        this.selectedOptions.set(optionKey, item.option)
        this.$forceUpdate()//vueset
        //
        //CommodityRightTop  ()
        // this.$bus.$emit("changePrice",item.optionPrice)
        // this.sendPrice()
        //   
        if (optionKey == "") {
          axios({
            method: 'get',
            url: '/getColorOptionImg/' + this.$route.params.commodityId + "/" + item.option
          }).then((response) => {
            //
            this.$bus.$emit("images", response.data.result)
          })
        }
        //
        // this.changeSty(event,index)
      },
      //
      changeSty(event, index) {
        var spans = event.target.parentElement.children
        for (var i = 0; i < spans.length; i++) {
          // console.log(spans[i]);
          if (i != index) {
            spans[i].className = "one"
          }
        }
        event.target.className = "one_active"
      },
      //
      sendPrice() {
        this.optionPrice = 0
        for (let i = 0; i < this.commodityOptions.length; i++) {
          const option = this.commodityOptions[i]
          for (let j = 0; j < option.options.length; j++) {
            const element = option.options[j];
            // this.optionPrice
            this.selectedOptions.forEach((value, key, set) => {  //element
              if (value == element.option && (element.optionPrice != null && element.optionPrice != 0)) {
                this.optionPrice += parseFloat(element.optionPrice)
              }
            })
          }
        }
        this.$bus.$emit("changePrice", this.sendPriceValue * this.commodityNumber)
      },
      //
      commodityNumberChange(operator) {
        if (operator == "+") {
          //
          if (this.commodity.limitNumber == this.commodityNumber) {
            this.showLimit = true
            this.showMessage = "" + this.commodity.limitNumber + ""
            return
          }
          this.showLimit = false
          this.commodityNumber++

          //1
        } else if (operator == "-") {
          if (this.commodityNumber <= 1) {
            this.showLimit = true
            this.showMessage = ""
          } else {
            this.showLimit = false
            this.commodityNumber--
          }
        }
        this.$bus.$emit("changePrice", this.sendPriceValue * this.commodityNumber)
      },
      toPay() {
        //
        //json
        if (localStorage.getItem("huawei_login") == null) {
          this.$router.push({
            path: '/login'
          })
          return
        }

        axios({
          method:'get',
          url:'/checkToken'
        }).then((response)=>{
          if(response.data.responseCode == 200){
            //
            if (confirm("")) {
              this.sendData(0)
            } else {
              this.sendData(1)
            }
          }
        })
      },
      toShopCar() {
        const user = JSON.parse(localStorage.getItem("huawei_login"))
        if (user == null) {
          this.$router.push({
            path: '/login'
          })
        } else {
          axios({
          method:'get',
          url:'/checkToken'
        }).then((response)=>{
          if(response.data.responseCode == 200){
            this.addShopCar()
          }
        })
          
        }

      },
      //
      sendData(Value) {
        //
        var strOptions = ''
        this.selectedOptions.forEach((value, key, set) => {
          strOptions += key + ":"
          strOptions += value + ","
        })
        axios({
          method: 'post',
          url: '/order/',
          params: {
            userId: this.$store.state.userState.id,
            orderPrice: this.sendPriceValue * this.commodityNumber,
            dfk: Value,
            commodityId: this.$route.params.commodityId,
            commodityOptions: strOptions,
            commodityNumber: this.commodityNumber,
            commodityPrice: this.sendPriceValue * this.commodityNumber
          }
        }).then((response) => {
          console.log(response.data);
          if (response.data.responseCode == 200 && Value == 0) {
            alert("")
          }else if(response.data.responseCode == 200 && Value == 1){
            alert("")
          }else if(response.data.responseCode == 201){
            alert(response.data.result.message)
          }
        })
      },
      //
      addShopCar() {
        var strOptions = ''
        this.selectedOptions.forEach((value, key, set) => {
          strOptions += key + ":"
          strOptions += value + ","
        })
        const user = JSON.parse(localStorage.getItem("huawei_login"))
        axios({
          method: 'post',
          url: '/addShopCar',
          params: {
            userId: user.id,
            scCommodityId: this.$route.params.commodityId,
            scCommodityOptions: strOptions,
            scCommodityNumber: this.commodityNumber,
            scCommodityPrice: this.sendPriceValue
          }
        }).then((response) => {
          //
          console.log(response.data);
          alert('')
          this.$bus.$emit('sentShopCarCommoditys',response.data.result)
        })
      },
      // 
      initializePrice(price, nowPrice) {
        console.log(price, nowPrice);
        if (nowPrice == 0 || nowPrice == null) {
          this.commodityPrice = price
        } else {
          this.commodityPrice = nowPrice
        }
      }
    },
    watch: {
      //commodity
      commodity(newValue, oldValue) {
        if (newValue.repertory == 0 || newValue == null) {
          this.$refs.placeOrder.setAttribute("disabled", "true")
        }
      }
    },
    mounted() {
      // optionselcetOptin
      axios({
        method: 'get',
        url: '/commodityDetail/' + this.$route.params.commodityId
      }).then((response) => {
        this.commodity = response.data.result
        this.initializePrice(this.commodity.price, this.commodity.nowPrice)
        var options = response.data.result.jsonOptions
        this.$bus.$emit("changePrice", this.optionPrice + this.commodityPrice)
        for (let i = 0; i < options.length; i++) {
          this.selcetOptin(options[i].optionName, options[i].options[0])
        }
      })
    },
  }
</script>
