import vue from 'vue'
import vuex from 'vuex'

vue.use(vuex)

//Actions
const actions = {
    setUser(context, user){
        context.commit("ADDUSERSTATE",user)
    }
}
//
//
const mutations = {
    ADDUSERSTATE(state,user){
        // console.log(context);
        state.userState = user
    }
}
//
const state = {
    userState:null,
    myorder:{
        orders:"woshi order"
    }
}


//storestore
export default new vuex.Store({
    //
    actions,
    mutations,
    state
})