<template>
    <div>
        <div class="commodity-outer">
            <div class="commodity-outer-inner">
                <img src="/images/seckill/timeCount.png"/>
                <p ></p>
                <p></p>
                <div>
                    {{countDownTime}}
                </div>
            </div>
        </div>
    </div>
</template>
<script>
let timer = null;
import moment from 'moment'
export default {
    name: 'CountDown',
    data() { return {
        currentTime: moment().format("YYYY-MM-DDTHH:mm"), // 1-3
        // currentTime: ,
        startTime: moment().format('2022-11-27T02:20'),
        countDownTime: '0 0 0 0'
    }},
    methods: {
        submit() {
            const _this = this;
            clearInterval(timer);
            timer = setInterval(() => {
                _this.countDownTime = _this.countDown();
            }, 1000);
        },
        countDown: function() {
            const seconds = moment(this.startTime).diff(new Date, 'seconds');
            if (seconds <= 0) {
                clearInterval(timer);
                return '';
            }
            const second = seconds%60;
            const minutes = (seconds-second) / 60;
            const minute = minutes%60;
            const hours = (minutes-minute) / 60;
            const hour = hours%24;
            const day = (hours-hour) / 24;
            const res =  day + ' '+ hour + ' '+ minute + ' '+ second + ' ';
            return res;
        }
    },
    mounted() {
        let s = new Date().getDay
        console.log(moment().format("YYYY-MM-DDTHH:mm:ss"));
        this.submit()
    },
}
</script>
<style scoped>
/*  */
.commodity-outer{
    width: 291px;
    height: 406px;
    display: inline-block;
    margin-right: 12px;
    background-color: rgba(207, 10, 44, 0.06);
    
}
.commodity-outer:hover{
    box-shadow: rgb(0 0 0 / 10%) 0px 0px 36px;
}
.commodity-outer-inner{
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
}
img{
    margin-top: 100px;
    height: 48px;
    width: 48px;
}

</style>